<template>
    <div class="product-page">
        <div class="product-page__wrapper">
            <div class="product-page__head">
                <div class="product-page__b-back" @click="back()">
                    <back/>
                </div>
                <input class="product-page__title" v-model="name" @keydown.enter="changeName(name)" @change="changeName(name)"/>
            </div>
            <div class="product-page__img-box">
                <div class="product-page__img">
                    <img :src="file" alt="">
                </div>
                <div class="product-page__b-img">
                    <label>Изменить изображение<input style="display:none;" type="file" name="f" ref="file" v-on:change="onFileChange()"></label>
                </div>
            </div>
            <div class="product-page__price-box">
                <input class="product-page__price" v-model="price"/>
                <span class="product-page__currency">₽</span>
            </div>
            <div class="product-page__status-box">
                <div class="product-page__status" :class="{ 'product-page__status--sales' : statusSale}">{{ statusSale ? 'Продано' : 'В наличии' }}</div>
                <div class="product-page__b-return" v-if="statusSale" @click="reeturn()" >Вернуть</div>
                <div class="product-page__b-sale" v-if="!statusSale" @click="sale()" >Продать</div>
            </div>
            <div class="product-page__description-box">
                <textarea @keydown.enter="changeDescription(description)" @change="changeDescription(description)" v-model="description" name="desc" type="text" placeholder="Описание..." class="product-page__description"></textarea>
            </div>
        </div>
    </div>
</template>



<script>
import Product from '../models/Product.js'
import back from './../assept/icons/back.vue'
export default {
  components:{
      back
  },
  name: 'finance',
  props: {
    id: {
      type: Number,
      default: null,
    },
    name: {
      type: String,
      default: '',
    },
    file: {
      type: String,
      default: 'data:image/png;base64,',
    },
    statusSale: {
      type: Boolean,
      default: false,
    },
    price: {
      type: Number,
      default: 0,
    },
    description: {
      type: String,
      default: '',
    },
  },
  data () {
    return {
    }
  },
  created(){
  },
  computed: {
  },
  methods: {
    onFileChange: function(e) {
        let file = this.$refs.file.files;
        if (!file.length)
            return;
        this.createImage(file[0]);
    },
    createImage: function(file) {
        let reader = new FileReader();
        let vm = this;
        reader.onload = function(e) {
            vm.file = e.target.result;
        };
        reader.readAsDataURL(file);
        
    },
    changeName(){
        Product.change(this.id, {
            name: this.name,
            'update_at': Date.now()
        });
    },
    changeDescription(){
        Product.change(this.id, {
            description: this.description,
            'update_at': Date.now()
        });
    },
    sale(){
      this.statusSale = true;
      Product.change(this.id, {
        statusSale: this.statusSale,
        'update_at': Date.now(),
      });
    },
    reeturn(){
      this.statusSale = false;
      Product.change(this.id, {
        statusSale: this.statusSale,
        update_at: Date.now()
      });
    },
    back(){
        this.$emit('close');
    }
  }
}
</script>

<style>
.product-page {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1000;
    background: #fff;
    height: 100%;
    width: 100%;
}
.product-page__wrapper {
    height: 100%;
    overflow: auto;
    padding-bottom: 15px;
    box-sizing: border-box;
}
.product-page__head {
    position: relative;
    height: 55px;
    box-sizing: border-box;
    padding: 1px;
}
.product-page__b-back {
    width: 35px;
    height: 35px;
    margin: 10px;
    border-radius: 30px;
    padding-top: 7px;
    padding-left: 5px;
    box-sizing: border-box;
    border: 1px solid #00c4ff;
    position: absolute;
}
.product-page__b-back svg {
    width: 20px;
    height: 20px;
    fill: #00c4ff;
}
.product-page__title {
    margin-left: 55px;
    height: 31px;
    line-height: 55px;
    font-size: 20px;
    color: #111;
    margin-top: 10px;
    padding-left: 5px;
    border-radius: 4px;
    border: 1px solid #ebebeb;
    width: 100%;
}
.product-page__img-box{
    position: relative;
}
.product-page__img {
    text-align: center;
}
.product-page__img img {
    width: 100%;
}
.product-page__price-box {
    text-align: center;
    margin-top: 15px;
}
input.product-page__price {
    border: 1px solid #ccc;
    border-radius: 4px;
    text-align: end;
    font-size: 29px;
    width: 100px;
    padding-right: 5px;
    text-align: center;
    margin-top: 10px;
}
.product-page__status-box {
    padding: 20px;
    line-height: 50px;
}
.product-page__status {
    display: inline;
    color: #25ad5e;
}
.product-page__b-sale {
    float: right;
    padding: 10px;
    height: 100%;
    padding: 0 5px;
    font-size: 14px;
    border-radius: 24px;
    line-height: 50px;
    width: 100px;
    text-align: center;
    height: 50px;
    background: #00c4ff;
    color: #fff;
}
.product-page__b-return{
  border: 1px solid #ccc;
  background: white;
  float: right;
    padding: 10px;
    height: 100%;
    padding: 0 5px;
    font-size: 14px;
    border-radius: 24px;
    line-height: 50px;
    width: 100px;
    text-align: center;
    height: 50px;
}
textarea.product-page__description {
    width: 98%;
    box-sizing: border-box;
    height: 100%;
    height: 100px;
    padding: 7px;
    box-sizing: border-box;
    margin-left: -3px;
    border-radius: 4px;
    border: 1px solid #ebebeb;
    padding-left: 7px;
}

.product-page__b-img {
    position: absolute;
    bottom: 0px;
    text-align: center;
    width: 100%;
    background: #000000b5;
    padding: 10px;
    color: white;
}
</style>



